.navbar-wrap {
  height: 80px;
  left: 0;
  right: 0;
  top: 0;
  z-index: 8000;
  width: 100%;
}
.navbar-wrap .navbar {
    height: 80px;
    background-color: #20a0ff;
    color: #fff;
    top: 0;
    left: 0;
    width: 100%;
    line-height: 80px;
    z-index: 100;
    position: relative;
}
@media screen and (min-width: 768px) and (max-width: 998px) {
.navbar-wrap .navbar {
        background: #ffffff;
        position: relative;
        z-index: 8000;
}
.navbar-wrap .navbar .navbar-logo {
          display: none;
}
.navbar-wrap .navbar .navbar-logo-small {
          display: inline-block;
}
.navbar-wrap .navbar .navbar-item {
          margin-left: 6px;
}
.navbar-wrap .navbar .navbar-item:last-child {
            margin-left: 10px;
}
.navbar-wrap .navbar .navbar-item a {
            padding: 0 5px;
}
}
@media screen and (max-width: 767px) {
.navbar-wrap .navbar {
        padding: 0 40px;
}
.navbar-wrap .navbar .container {
          padding: 0 12px;
}
.navbar-wrap .navbar .navbar-item a,
        .navbar-wrap .navbar .navbar-lang {
          font-size: 12px;
          vertical-align: top;
}
}
.navbar-wrap .navbar .container {
      height: 100%;
      box-sizing: border-box;
}
.navbar-wrap .navbar .navbar-brand, .navbar-wrap .navbar h1 {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      margin: 0;
      float: left;
      font-size: 32px;
      font-weight: normal;
}
.navbar-wrap .navbar .navbar-brand a, .navbar-wrap .navbar h1 a {
        color: #fff;
        display: inline-block;
        text-decoration: none;
        font-weight: 700;
        transition: color 0.125s ease-in-out 0s;
        -moz-transition: color 0.125s ease-in-out 0s;
        -webkit-transition: color 0.125s ease-in-out 0s;
        -o-transition: color 0.125s ease-in-out 0s;
}
.navbar-wrap .navbar .navbar-brand img, .navbar-wrap .navbar .navbar-brand span, .navbar-wrap .navbar h1 img, .navbar-wrap .navbar h1 span {
        display: inline-block;
        height: 32px;
        vertical-align: middle;
}
.navbar-wrap .navbar .navbar-brand span, .navbar-wrap .navbar h1 span {
        font-size: 12px;
        width: 34px;
        height: 18px;
        text-align: center;
        line-height: 18px;
        vertical-align: middle;
        margin-left: 10px;
}
.navbar-wrap .navbar .navbar-brand .navbar-brand-logo,
    .navbar-wrap .navbar .navbar-brand .navbar-brand-logo-small {
      vertical-align: sub;
}
.navbar-wrap .navbar .navbar-brand .navbar-brand-logo-small {
      display: none;
}
.navbar-wrap .navbar .navbar-menu {
      float: right;
      height: 100%;
      line-height: 80px;
      background: transparent;
      padding: 0;
      margin: 0;
}
.navbar-wrap .navbar .navbar-menu:before, .navbar-wrap .navbar .navbar-menu:after {
        content: " ";
        display: table;
}
.navbar-wrap .navbar .navbar-menu:after {
        clear: both;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item {
        display: inline-block;
        float: left;
        list-style: none;
        position: relative;
        cursor: pointer;
        margin: 0 0 0 20px;
        font-size: 15px;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item:last-child {
          margin-left: 34px;
          cursor: default;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item:last-child span {
            opacity: .8;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item:last-child .navbar-lang {
            cursor: pointer;
            display: inline-block;
            height: 100%;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item:last-child .navbar-lang:hover {
              opacity: 1;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item:last-child .navbar-lang.is-active {
              font-weight: 700;
              opacity: 1;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item a, .navbar-wrap .navbar .navbar-menu .navbar-menu-item span {
          color: #fff;
          font-size: 15px;
          font-weight: 400;
          text-decoration: none;
          opacity: .8;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item a.is-active, .navbar-wrap .navbar .navbar-menu .navbar-menu-item a:hover, .navbar-wrap .navbar .navbar-menu .navbar-menu-item a.router-link-active, .navbar-wrap .navbar .navbar-menu .navbar-menu-item span.is-active, .navbar-wrap .navbar .navbar-menu .navbar-menu-item span:hover, .navbar-wrap .navbar .navbar-menu .navbar-menu-item span.router-link-active {
            border-bottom: 4px solid #99d2fc;
            padding-bottom: 26px;
            font-weight: 700;
            opacity: 1;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item a.is-active, .navbar-wrap .navbar .navbar-menu .navbar-menu-item a.router-link-active, .navbar-wrap .navbar .navbar-menu .navbar-menu-item span.is-active, .navbar-wrap .navbar .navbar-menu .navbar-menu-item span.router-link-active {
            font-weight: 700;
            color: #fff;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item a.is-active::before, .navbar-wrap .navbar .navbar-menu .navbar-menu-item a.router-link-active::before, .navbar-wrap .navbar .navbar-menu .navbar-menu-item span.is-active::before, .navbar-wrap .navbar .navbar-menu .navbar-menu-item span.router-link-active::before {
            content: '';
            display: block;
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 4px;
            background: #99d2fc;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item.navbar-dropdown-container {
          position: relative;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item.navbar-dropdown-container > a, .navbar-wrap .navbar .navbar-menu .navbar-menu-item.navbar-dropdown-container > span {
            color: #fff;
            position: relative;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item.navbar-dropdown-container > a:after, .navbar-wrap .navbar .navbar-menu .navbar-menu-item.navbar-dropdown-container > span:after {
              content: '';
              display: inline-block;
              vertical-align: middle;
              margin-top: -1px;
              margin-left: 6px;
              width: 0;
              height: 0;
              border-left: 4px solid transparent;
              border-right: 4px solid transparent;
              border-top: 5px solid #ccc;
              transition: -webkit-transform .3s ease-in-out;
              transition: transform .3s ease-in-out;
              transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
              position: absolute;
              right: -14px;
              top: 9px;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item.navbar-dropdown-container:hover .navbar-dropdown {
            left: -15px;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item.navbar-dropdown-container:hover > a:after,
          .navbar-wrap .navbar .navbar-menu .navbar-menu-item.navbar-dropdown-container:hover > span:after {
            -webkit-transform: rotateZ(180deg);
                    transform: rotateZ(180deg);
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item.navbar-dropdown-container .navbar-dropdown {
            position: absolute;
            top: 100%;
            left: -9000px;
            background: #ffffff;
            padding: 10px 0;
            border: 1px solid #dddddd;
            border-bottom-color: #ccc;
            border-radius: 4px;
            -moz-border-radius: 4px;
            -webkit-border-radius: 4px;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item.navbar-dropdown-container .navbar-dropdown li {
              display: block;
              line-height: 28px;
              margin: 0;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item.navbar-dropdown-container .navbar-dropdown a {
              display: block;
              font-size: .9em;
              padding: 0 1.4em;
              color: #636d7e;
}
.navbar-wrap .navbar .navbar-menu .navbar-menu-item.navbar-dropdown-container .navbar-dropdown a:hover {
                color: #85bdfc;
                border-bottom: none;
                padding: 0 1.4em;
}
@media screen and (min-width: 768px) and (max-width: 998px) {
.navbar-wrap .navbar .navbar-menu .navbar-menu-item {
            font-size: 32px;
}
}
.navbar-wrap .navbar.navbar-home {
      position: fixed;
      top: 0;
      background-color: rgba(32, 160, 255, 0);
}
@media screen and (max-width: 1059px) {
.navbar-wrap .navbar .navbar-dropdown-container:hover .navbar-dropdown {
        left: auto;
        right: -30px;
}
}
.navbar-wrap .menu-icon {
    cursor: pointer;
    display: none;
    height: 32px;
    width: 32px;
    position: absolute;
    left: 10px;
    top: 23px;
    z-index: 9000;
}
.navbar-wrap .side-menu {
    display: none;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    background: #f7f7f7;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
    height: 100%;
    width: 260px;
    padding: 0 8px;
    overflow-x: hidden;
    overflow-y: auto;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 7000;
    transform: translate(-280px, 0);
    -moz-transform: translate(-280px, 0);
    -ms-transform: translate(-280px, 0);
    -o-transform: translate(-280px, 0);
    -webkit-transform: translate(-280px, 0);
    transition: -webkit-transform 0.4s cubic-bezier(0.4, 0, 0, 1);
    transition: transform 0.4s cubic-bezier(0.4, 0, 0, 1);
    transition: transform 0.4s cubic-bezier(0.4, 0, 0, 1), -webkit-transform 0.4s cubic-bezier(0.4, 0, 0, 1);
    -moz-transition: transform 0.4s cubic-bezier(0.4, 0, 0, 1);
    -webkit-transition: transform 0.4s cubic-bezier(0.4, 0, 0, 1);
    -o-transition: transform 0.4s cubic-bezier(0.4, 0, 0, 1);
}
.navbar-wrap .side-menu ul {
      height: 100%;
      overflow-y: auto;
      padding-top: 60px;
      padding-bottom: 10px;
}
.navbar-wrap .side-menu ul li {
        display: block;
        margin: 0;
}
.navbar-wrap .side-menu ul .side-brand {
        line-height: 48px;
}
.navbar-wrap .side-menu ul h2 {
        margin: 0;
        padding-left: .7em;
}
.navbar-wrap .side-menu ul a, .navbar-wrap .side-menu ul span {
        display: block;
        border-bottom: none;
        padding: 0 1em;
}
.navbar-wrap .side-menu ul a {
        color: #34495e;
}
.navbar-wrap .side-menu ul span {
        color: #7f8c8d;
}
.navbar-wrap .side-menu ul .side-dropdown-container > a:after,
      .navbar-wrap .side-menu ul .side-dropdown-container > span:after {
        content: '';
        display: inline-block;
        vertical-align: middle;
        margin-top: -1px;
        margin-left: 6px;
        width: 0;
        height: 0;
        border-left: 4px solid transparent;
        border-right: 4px solid transparent;
        border-top: 5px solid #ccc;
}
.navbar-wrap .side-menu ul a:hover, .navbar-wrap .side-menu ul a.router-link-active {
        color: #42b983;
        padding-bottom: 0;
}
.navbar-wrap .side-menu ul a.router-link-active {
        font-weight: 600;
}
.navbar-wrap .side-menu ul .side-dropdown {
        padding: 0 0 0 15px;
}
.navbar-wrap .side-menu-backdrop {
    display: none;
    background: rgba(0, 0, 0, 0.25);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 6000;
}
@media screen and (max-width: 767px) {
.navbar-wrap .navbar .container .navbar-brand {
      display: none;
}
.navbar-wrap .navbar .container .navbar-brand a, .navbar-wrap .navbar .container .navbar-brand img {
        display: block;
        margin: 0 auto;
        width: 32px;
}
.navbar-wrap .navbar .container .navbar-brand span {
        display: none;
}
.navbar-wrap .navbar .navbar-menu {
      display: none;
}
.navbar-wrap .menu-icon {
      display: block;
}
.navbar-wrap .side-menu {
      display: block;
}
.navbar-wrap .side-menu-open {
      transform: translate(0, 0);
      -moz-transform: translate(0, 0);
      -ms-transform: translate(0, 0);
      -o-transform: translate(0, 0);
      -webkit-transform: translate(0, 0);
}
.navbar-wrap .side-menu-backdrop {
      display: block;
}
}
@media screen and (min-width: 768px) and (max-width: 998px) {
.app-home-view header {
    border: none;
    padding: 0;
}
.app-home-view .navbar-brand {
    display: none;
}
}



