.m-button {
  padding: 0 0.24rem;
}
.btn {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #ffffff;
  position: relative;
  border: 1px solid #bfcbd9;
  color: #1f2d3d;
  pointer-events: auto;
  -webkit-appearance: none;
  text-align: center;
  height: 41px;
  margin: 0;
  box-sizing: border-box;
  outline: none;
  overflow: hidden;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: transparent;
  padding: 10px 15px;
  font-size: 14px;
  border-radius: 4px;
}
.btn:hover, .btn:focus {
    color: #20a0ff;
    border-color: color(#001220 h(3) s(16%) l(58%));
}
.btn:active {
    color: shade(#20a0ff, 10%);
    border-color: #bfcbd9;
    outline: none;
}
.btn::-moz-focus-inner {
    border: 0;
}
.btn [class*="btn-icon-"] + span {
    margin-left: 5px;
}
.btn + [class*="btn-"] {
    margin-left: 10px;
}



